import React, { useEffect } from "react";
import customer1 from "../../styles/assets/images/customers/dave.jpg";
import customer2 from "../../styles/assets/images/customers/ben.jpg";
import customer3 from "../../styles/assets/images/customers/steve.jpg";
import customer4 from "../../styles/assets/images/customers/hannah.jpg";

const Testimonials = () => {
  return (
    <section className="section-testimonials" id="testimonial">
      <span className="bottomAnimation subheading servicehead">Testimonials</span>
      <h2 className="bottomAnimation heading-secondary servicehead">
        Once you try it, you can't go back
      </h2>
      <div className="testimonials-container grid grid--4-cols">
        <div className="rightAnimation testimonials">
          <figure className=" testimonial">
            <img
              className="testimonial-img"
              alt="Photo of customer Dave Bryson"
              src={customer1}
            />
            <blockquote className="testimonial-text">
              It is a distinct pleasure for me to recommend Thrive Internet
              Marketing to any and all interested parties. They have been
              professional, comprehensive and competent throughout the process
              of our working together.
            </blockquote>
            <p className="testimonial-name">
              <strong>&mdash; Dave Bryson</strong>
            </p>
          </figure>
        </div>
        <div className="rightAnimation testimonials">
          <figure className="testimonial">
            <img
              className="testimonial-img"
              alt="Photo of customer Ben Hadley"
              src={customer2}
            />
            <blockquote className="testimonial-text">
              We feel that we have established a relationship with them for
              years to come. The reaction to our new web site has been
              overwhelmingly positive; as one commented the site is FANTASTIC.
              The same can be said for our view of Thrives work for us.
            </blockquote>
            <p className="testimonial-name">
              <strong>&mdash; Ben Hadley</strong>
            </p>
          </figure>
        </div>
        <div className="rightAnimation testimonials">
          <figure className="testimonial">
            <img
              className="testimonial-img"
              alt="Photo of customer Steve Miller"
              src={customer3}
            />
            <blockquote className="testimonial-text">
              It is a distinct pleasure for me to recommend Thrive Internet
              Marketing to any and all interested parties. They have been
              professional, comprehensive and competent throughout the process
              of our working together.
            </blockquote>
            <p className="testimonial-name">
              <strong>&mdash; Steve Miller</strong>
            </p>
          </figure>
        </div>
        <div className="rightAnimation testimonials">
          <figure className="testimonial">
            <img
              className="testimonial-img"
              alt="Photo of customer Hannah Smith"
              src={customer4}
            />
            <blockquote className="testimonial-text">
              We feel that we have established a relationship with them for
              years to come. The reaction to our new web site has been
              overwhelmingly positive; as one commented the site is FANTASTIC.
              The same can be said for our view of Thrives work for us.
            </blockquote>
            <p className="testimonial-name">
              <strong>&mdash; Hannah Smith</strong>
            </p>
          </figure>
        </div>
      </div>
    </section>
  );
};

export default Testimonials;
