import React from "react";
import technology from "../../styles/assets/images/ServiceLogo/webdevelop.png";
import react from "../../styles/assets/images/FramworksHD/React.png";
import angular from "../../styles/assets/images/FramworksHD/Angular.png";
import js from "../../styles/assets/images/FramworksHD/js.png";
import solidity from "../../styles/assets/images/FramworksHD/solidity.avif";
import node from "../../styles/assets/images/FramworksHD/node-js.png";
import mongodb from "../../styles/assets/images/FramworksHD/mongodb.avif";

const OurDevelopment = () => {
  return (
    <section className="section-tech py-5">
      <div className="container">
        <div className="row d-flex align-items-center justify-content-center">
          <h3 className="text-center text-uppercase">
            Our Development Technologies
          </h3>
        </div>
        <div className="row">
          <div className="col-md-6 col-sm-12 col-12 d-flex flex-column justify-content-center align-items-center">
            <img
              className=" w-50"
              src={technology}
              alt="technology-img"
            />
            <p className="fs-1">See your technologies</p>
          </div>
          <div className="col-md-6 p-5 col-sm-12 col-12">
            <div className="row d-flex justify-content-center align-items-center">
              <div className="col-md-4 col-sm-4 col-4">
                <figure className="gallery-item">
                  <img
                    className=" techstack-img"
                    src={react}
                    alt={"react-img"}
                  />
                  <figcaption className="">React</figcaption>
                </figure>
              </div>
              <div className="col-md-4 col-sm-4 col-4">
                <figure className="gallery-item">
                  <img
                    className=" techstack-img"
                    src={angular}
                    alt="angular-img"
                  />
                  <figcaption className="">Angular</figcaption>
                </figure>
              </div>
              <div className="col-md-4 col-sm-4 col-4">
                <figure className="gallery-item">
                  <img
                    className=" techstack-img"
                    src={js}
                    alt="js-img"
                  />
                  <figcaption className="">Express</figcaption>
                </figure>
              </div>
              <div className="col-md-4 col-sm-4 col-4">
                <figure className="gallery-item">
                  <img
                    className=" techstack-img"
                    src={solidity}
                    alt="solidity-img"
                  />
                  <figcaption className="">Solidity</figcaption>
                </figure>
              </div>
              <div className="col-md-4 col-sm-4 col-4">
                <figure className="gallery-item">
                  <img
                    className=" techstack-img"
                    src={node}
                    alt="node-img"
                  />
                  <figcaption className="">Node</figcaption>
                </figure>
              </div>
              <div className="col-md-4 col-sm-4 col-4">
                <figure className="gallery-item">
                  <img
                    className=" techstack-img"
                    src={mongodb}
                    alt="mongodb-img"
                  />
                  <figcaption className="">Mongo</figcaption>
                </figure>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default OurDevelopment;
