import React from "react";
import jsImg from "../../styles/assets/images/Frameworks/javascript.svg";
import reactImg from "../../styles/assets/images/FramworksHD/React.png";
import angularImg from "../../styles/assets/images/Frameworks/angular.svg";
import nodeImg from "../../styles/assets/images/Frameworks/node.svg";
import mongoImg from "../../styles/assets/images/FramworksHD/mongodb.avif";
import solidityImg from "../../styles/assets/images/Frameworks/solidity.png";

const Popular = () => {
  return (
    <section className="section-featured p-5">
      <div className="container">
        <div className="row">
          <h3 className="rightAnimation heading-fourth text-center">
            POPULAR IN
          </h3>
          <div className="logos"></div>
        </div>
        <div className="container">
          <div className="row p-5">
            <div className="col-md-2 col-sm-4 col-4 d-flex flex-column align-items-center justify-content-center">
              <img src={reactImg} alt="react" className="w-50"/>
              <p className="feature-description">React</p>
            </div>
            <div className="col-md-2 col-sm-4 col-4 d-flex flex-column align-items-center justify-content-center">
              <img src={jsImg} alt="javascript" />
              <p className="feature-description">Javascript</p>
            </div>
            <div className="col-md-2 col-sm-4 col-4 d-flex flex-column align-items-center justify-content-center">
              <img src={angularImg} alt="angualr" />
              <p className="feature-description">Angular</p>
            </div>
            <div className="col-md-2 col-sm-4 col-4 d-flex flex-column align-items-center justify-content-center">
              <img src={nodeImg} alt="node" />
              <p className="feature-description">Node</p>
            </div>
            <div className="col-md-2 col-sm-4 col-4 d-flex flex-column align-items-center justify-content-center">
              <img src={mongoImg} alt="mongo" />
              <p className="feature-description">Mongo</p>
            </div>
            <div className="col-md-2 col-sm-4 col-4 py-xs-5 d-flex flex-column align-items-center justify-content-center">
              <img src={solidityImg} alt="solidity" />
              <p className="feature-description">Solidity</p>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Popular;
